body {
	background : gray;
	font-family: Tahoma, Verdana, Arial;
	font-size: 10pt;
	margin: 5px;
}
/* ie focusing elements */
input:focus, textarea:focus {
	outline: none;
}
 
#treePagesEditor .nav-tabs{
	margin-bottom: 11px;
}

#pages-editor-tab{
	margin: 0px;
	padding: 0px;
	min-height: 510px;
}  
.markItUpEditor{
	border-width: 1px;
	margin: 0px -10px 10px 4px;
	min-height: 430px;
}  
#markItUpPages-editor{
	margin: 0px;
}
.markItUpHeader{
	margin-left: 10px;
}
#pages-editor::-webkit-scrollbar {
	width: 12px;
	height: 12px;
}
#pages-editor::-webkit-scrollbar-button:start:decrement,
#pages-editor::-webkit-scrollbar-button:end:increment  {
	height: 10px;
	display: block;
	background-color: transparent;
}
#pages-editor::-webkit-scrollbar-track-piece  {
background-color: #ccc;
	-webkit-border-radius: 6px;
}
#pages-editor::-webkit-scrollbar-thumb:vertical {
	height: 50px;
	background-color: #666;
	border: 1px solid #eee;
	-webkit-border-radius: 6px;
}

#pages-editor {
	position: relative !important;
	height: 100%;
	width: 100%;
}

#pages-editor-container {
	min-width: 470px;
	width: 99%;
	min-height: 480px;
	height: 480px;
}

#pages-editor-buttons {
	font-size: 0.8em;
	padding: 0px 3px 0px 10px;
}
#pages-editor-buttons span {
	display: inline-block;
	border: 1px solid lightgray;
	cursor: pointer;
	-webkit-border-radius:  3px;
	-moz-border-radius:  3px;
	border-radius: 3px;
	min-height: 18px;
	min-width: 18px;
	text-align: center;
	padding: 0px 3px 1px 3px;
	margin: 0px 0px 7px 0px;
}
#pages-editor-buttons-Link, #pages-editor-buttons-Image, #pages-editor-buttons-Help{
	border: 1px solid red;
	padding: 0px 7px 1px 7px !important;
}

#pages-editor-buttons span:hover {
	background: #EFEFEF;
}

#tabsContent {
	min-width: 950px;
	min-height: 700px;
}
#tabsContent ul.tabs li{
	font-size: 9pt;
	font-weight: bold;
	margin: 3px 3px 0px 3px;
	padding: 1px 7px 0px 7px;
}
#tabsContent ul.tabs li.ui-tabs-selected{
	margin: 3px 3px -3px 3px !important;
}
#tabsContent ul.tabs li a{
	color: #aaa;
}
#tabsContent ul.tabs li.ui-tabs-selected  a{
	color: black !important;
}
#tabsContent ul.tabs li b.logo{
	font-size: 18pt;
	color: #eee;
	margin: 3px 190px 2px 20px;
	display: block;
}

#treePages {
	min-height: 570px;
	height: 570px;
	overflow: auto;
	width: 300px;
	font-size: 9pt;
	margin: 5px;
	padding: 10px 2px 8px 0px;
	border: 1px solid gray;
}
#treePagesEditor {
	border: 1px solid gray;
	margin: 5px;
	min-height: 565px;
	padding: 11px;
}
#templsButtons {
	min-height: 570px;
	border: 1px solid gray;
	padding: 10px 10px 20px 10px;
	margin-bottom: 10px;
}
.el-finder-dialog textarea{
	width: 98% !important;
}
#templsButtons ul.nav-tabs {
	padding: 0px 20px 0px 0px;
	margin: 0px 0px 0px 20px;
}
#templsButtons ul.nav-tabs > li > a {
	padding: 5px 11px 4px 11px;
	margin: 0px 0px 0px 20px;
	color: #aaa;
}
#templsButtons ul.nav-tabs > li.active > a{
	color: #555;
	font-weight: bold;
}
#templManager-template-container{
	margin-left: 10px;
}

#templ-source-editor {
	position: relative !important;
	height: 100%;
	width: 100%;
}
#templ-source-container {
	min-width: 870px;
	min-height: 490px;
	padding: 10px 10px 20px 10px;
	width: 95%;
	height: 490px;
}

#templHeader-tab {
	position: relative;
	min-width: 400px;
	padding: 0px;
	margin: 0px;
}
#templ-snippet-tab {
	position: relative;
	min-width: 400px;
	overflow: auto;
	padding: 0px;
	margin: 0px;
}
#templ-snippet-editor {
	margin: 10px 0px;
	min-width: 820px;
	min-height: 100px;
	font-size: 9px;
}
#snippeteditor {
	margin: 0px;
	padding: 7px 10px 15px 20px;
	border: 1px dashed #ccc;
	width: 90%;
	height: 358px;
	overflow: auto;
}

#treePagesEditor ul.nav-tabs {
	padding: 3px 10px 0px 95px;
}
#treePagesEditor ul.nav-tabs > li > a{
	line-height: 12px !important;
	font-size: 9pt;
	color: #aaa;
}
#treePagesEditor ul.nav-tabs > li.active > a{
	color: #555;
	font-weight: bold;

}
#treePagesEditor-title, #treePagesEditor-keywords, #treePagesEditor-description, #treePagesEditor-additional,
#templHeader-title, #templHeader-keywords, #templHeader-description, #templHeader-additional {
	font-size: 0.9em;
	min-width: 98%;
	color: #555;
}
#treePagesEditor-additional, #treePagesEditor-description, #templHeader-description, #templHeader-additional {
	min-height: 100px;
}
#treePagesEditor-info {
	font-size: 0.9em;
	width: 94%;
	color: #555;
}
#treePagesEditor-info-snippet{
	margin: 0px;
	padding: 5px 10px 10px 15px;
	border: 1px dashed #ccc;
	width: 90%;
	height: 358px;
	overflow: auto;
}

#settingsTab label , #pageInfo label, #pageInfo pre{
	min-width: 95%;
	color: #555;
	font-size: 8pt !important;
	font-weight: bold;
}
#pageInfo pre {
	font-family: Tahoma, Arial, Verdana;
	line-height: 9pt;
}

#treeToolbar {
	border: 1px solid gray;
	margin: 5px 0px;
	padding: 3px;
	width: 22px;
	display: block;
}
#treeToolbar hr{
	width: 12px; margin: 3px; padding: 0px;
}
#treeToolbar span{
	margin: 1px;
	display: inline-block;
	width: 18px;
	height: 18px;
}
#treeToolbar span.ui-button-icon-primary {
	margin: -8px;
}
#tree-dialog-leaf-add-form-checkbox-div{ display: none; }



.ace_editor {
	border: 1px solid lightgray;
}
