.ace_editor.fullScreen {
	height: auto;
	width: auto;
	border: 0;
	margin: 0;
	position: fixed !important;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 10;
	background: white;
}

.fullScreen {
	overflow: hidden
}

#ace_theme {
	width: 175px;
	padding: 2px 1px 1px 7px;
	font-size: 0.9em;
	height: 22px;
	margin: -1px 0px 0px 10px;
}

#settings-editor-text {
	position: relative !important;
	width: 95%;
	height: 100%;
}
#settings-editor-text-container {
	min-width: 870px;
	min-height: 400px;
	width: 90%;
	height: 390px;
}
body {
	margin: 10px 5px 0px 5px;
	min-width: 950px;
	overflow-y: scroll;
}

.white {
	background: white;
}
div.logo {
	min-width: 800px;
	height: 110px;
	padding: 20px 20px 20px 70px;
	text-align: center;
}

ul.left_menu li a {
	display: inline-block;
	padding: 3px 19px 2px 17px;
	margin: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;	
}
ul.left_menu li a.active {
	background: #ddd;
}

ul.left_menu.documents li a {
	margin: 0px;
	padding: 2px 8px 1px 10px;
}
ul.left_menu li.divider {
	margin: 7px 5px 5px -15px;
	padding: 0px;
	list-style: none;
	border-bottom: 1px dotted gray;
}



div.documentationHeader {
	padding: 11px 20px 11px 20px;
}